import React from 'react';
import { NavLink } from 'react-router-dom';
import { connect } from 'react-redux';
import Lunbo from './Lunbo';
import {goodsList} from './actionCreators';
import './home.css'


const Home = (props) => {
	
	props.dispatch(goodsList())

	return (
		<div className='home'>
			<div className='home_search'>
				<i className='iconfont icon-taobao' style={{position:'absolute',left:'3%',color:'white',fontSize:'250%'}}></i>
				<NavLink to='/home/search'><input type="text" value="寻找宝贝店铺" className='home_inp'/></NavLink>
				<i className='iconfont icon-sousuo' style={{position:'absolute',left:'38%',top:'30%',color:'white'}}></i>
			</div>
			<div className='home_content'>
				<Lunbo />
				<div className='home_xihuan'>
					<i className='iconfont icon-aixin'></i>
					你可能还喜欢
				</div>
				<div className='home_photo'>
					{
						props.datalist.map((item,idx)=>(
							<NavLink to={{pathname:'/home/goods',data:props.datalist,item:item}}>
								<div className='home_photo_shop'>
									<img src={item.img} />
									<div className='home_box'>{item.title}</div>
									<div>
										<div className='how_much'>￥{item.price}</div>
										<div className='how_many'>{item.id}人已购买</div>
									</div>
								</div>
							</NavLink>
						))
					}
				</div>
			</div>
		</div>
	)
	
}

export default connect(store=>{
	return store;
})(Home)