import React from 'react';

const Search = (props)=>{
    return <div style={{position:'fixed',backgroundColor:'white',width:'100%',height:"45px"}}>
        <div style={{border:0,borderRadius:'5px',backgroundColor:'#E3E3E5',
            position:'relative',width:'300px',height:'32px',left:'14px',top:'6px'}}
        >
            <form style={{float:'left',marginTop:'5px'}}>
                <select style={{backgroundColor:'#E3E3E5',border:0,color:'grey'}}>
                    <option selected="selected">宝贝</option>
                    <option>店铺</option>
                    <option>天猫</option>
                </select>
            </form>
            <input style={{border:0,position:'fixed',top:'13px',left:'70px',backgroundColor:'#E3E3E5',width:'200px'}}
                ></input>
            <i className='iconfont icon-sousuo'
                style={{position:'fixed',top:'10px',left:'280px',fontSize:'23px'}}></i>
            <div style={{marginLeft:'300px',height:'30px',width:'67px',lineHeight:'34px',textAlign:'center',color:'grey'}}
                onClick={props.history.goBack}
            >取消
            </div>
        </div>
    </div>
}

export default Search;