import React from 'react';
import { Carousel } from "antd-mobile";

class Lunbo extends React.Component {
  state = {
    data: ['https://gw.alicdn.com/imgextra/i3/191/O1CN01VWfpT01DHWHQurTZ7_!!191-0-lubanu.jpg',
           'https://gw.alicdn.com/imgextra/i2/87/O1CN015w4ZpM1CVt5SJMwF8_!!87-0-lubanu.jpg',
           'https://aecpm.alicdn.com/simba/img/TB1CWf9KpXXXXbuXpXXSutbFXXX.jpg_q50.jpg',
           'https://aecpm.alicdn.com/simba/img/TB15tIjGVXXXXcoapXXSutbFXXX.jpg',
           'https://gw.alicdn.com/imgextra/i2/84/O1CN01SDz8671CUVtsDPOYB_!!84-0-ppp-picassogw.jpg'],
    imgHeight: 105,
  }
  componentDidMount() {
    // simulate img loading
    setTimeout(() => {
      this.setState({
        data: ['https://gw.alicdn.com/imgextra/i3/191/O1CN01VWfpT01DHWHQurTZ7_!!191-0-lubanu.jpg',
               'https://gw.alicdn.com/imgextra/i2/87/O1CN015w4ZpM1CVt5SJMwF8_!!87-0-lubanu.jpg',
               'https://aecpm.alicdn.com/simba/img/TB1CWf9KpXXXXbuXpXXSutbFXXX.jpg_q50.jpg',
               'https://aecpm.alicdn.com/simba/img/TB15tIjGVXXXXcoapXXSutbFXXX.jpg',
               'https://gw.alicdn.com/imgextra/i2/84/O1CN01SDz8671CUVtsDPOYB_!!84-0-ppp-picassogw.jpg'],
      });
    }, 100);
  }
  render() {
    return (<div style={{paddingTop:'40px'}}>
      <Carousel
        autoplay={true}
        infinite
      >
        {this.state.data.map(val => (
          <a
            key={val}
            href="http://www.taobao.com"
            style={{ width: '100%', height: this.state.imgHeight }}
          >
          <img
            src={`${val}`}
            alt=""
            style={{ width: '100%'}}
            onLoad={() => {
              // fire window resize event to change height
              window.dispatchEvent(new Event('resize'));
              this.setState({ height: this.state.imgHeight });
            }}
          />
          </a>
        ))}
      </Carousel>
    </div>)
  }
}

export default Lunbo
