import React from 'react';
import {connect} from 'react-redux';
import { del,login } from './actionCreators'


const Shoucang = (props)=>{

    if(!props.useinfo){
        props.history.push('/login')
    }
    console.log(props.shoucang)

    return (
        <div style={{width:'100%'}}>
            <h2 style={{textAlign:'center'}}>我的收藏</h2>
            {
                props.shoucang.map((item,idx)=>{
                    return <div style={{bottom:'50px',float:'left',backgroundColor:'pink',width:'100%',height:'170px'}}>
                        <div style={{marginLeft:'20px',marginTop:'14px',float:'left',width:'255px',height:'150px'}}><img src={item.img} style={{width:'245px',height:'140px',borderRadius:'10px'}}/></div>
                        <div onClick={()=>(props.dispatch(del(item)),
                                props.history.push('/shoucang'))}
                            style={{float:'left',width:'80px',height:'150px',textAlign:'center',lineHeight:'150px',fontSize:'20px',color:'blue'}}>
                        取消收藏</div>
                    </div>
                })
            }
        </div>
    )
}



export default connect(store=>{
	return store;
})(Shoucang)