import React from 'react';
import {withRouter} from 'react-router-dom';
import {TabBar} from 'antd-mobile'

class Mytab extends React.Component {
	constructor(props) {
	  	super(props);
	  	this.state = {
			tableItemIndex:0,
		};
	}


	render() {

	  const {pathname} = this.props.location;
	  if(pathname.split('/').length > 2){
		return null;
	  }

	  return (
		<div style={{width:'100%',height:'70px',position:'fixed',bottom:'-1px'}}>
		  <TabBar unselectedTintColor="#949494" tintColor="#111213" barTintColor="white">
			<TabBar.Item className='bar'
			  	title="首页"
			  	key="home"
			  	icon={
				  	<i className='iconfont icon-shouye'></i>
			  	}
			  	selectedIcon={
				  	<i className='iconfont icon-shouye1'></i>
			  	}
			  	selected={pathname === '/'}
			  	onPress={() => {
					this.props.history.push('/')
			  	}}
			  	data-seed="logId"
			>
			</TabBar.Item>
			<TabBar.Item
			  	icon={
					<i className='iconfont icon-gouwuche1'></i>
			  	}
			  	selectedIcon={
					<i className='iconfont icon-gouwuche'></i>
			  	}
			  	title="收藏"
			  	key="like"
			  	selected={pathname === '/shoucang'}
			  	onPress={() => {
					this.props.history.push('/shoucang');
			  	}}
			  	data-seed="logId1"
			>
			</TabBar.Item>
			<TabBar.Item
				icon={
					<i className='iconfont icon-dingdanliebiao'></i>
				}
				selectedIcon={
					<i className='iconfont icon-dingdanliebiao-tianchong'></i>
				}
				title="订单列表"
				key="list"
				selected={pathname === '/list'}
				onPress={() => {
					this.props.history.push('/list')
				}}
				data-seed="logId2"
			>
			</TabBar.Item>
			<TabBar.Item
				icon={
					<i className='iconfont icon-wode1'></i>
				}
				selectedIcon={
					<i className='iconfont icon-wode'></i>
				}
				title="我的淘宝"
				key="my"
				selected={pathname === '/my'}
				onPress={() => {
					this.props.history.push('/my')
				}}
				data-seed="logId3"
			>
			</TabBar.Item>
		  </TabBar>
		</div>
	  );
	}
}
export default withRouter(Mytab);