import React from 'react'
import { login } from './actionCreators'
import { connect } from 'react-redux'

const Pleaselogin = (props)=>{

    props.dispatch(login())
    console.log(props)
    
    function goLogin(){
        let content = document.getElementById("inp");
        console.log(content.value)
        if(content.value === props.useinfo){
            alert('登录成功')
            console.log('登录成功')
            // props.dispatch(login())
            // console.log(props)
            props.history.push('/shoucang')
        }else{
            alert('亲，请先登录哦')
            console.log('请登录')
        }
    }

    return <div>
        <div><input type="text" placeholder="输入姓名" id="inp"
                style={{marginLeft:'30px',marginTop:'50px',width:'300px',height:'35px'}}
            ></input>
        </div>
        <div><button type="button"
                style={{marginLeft:'30px',width:'308px',height:'35px'}}
                onClick={()=>{goLogin()}}
            >登录</button>
        </div>
    </div>
}

export default connect(store=>{
	return store;
})(Pleaselogin)