import { NavLink } from 'react-router-dom';
import {connect} from 'react-redux'
import React from 'react';
import './goods.css'


const Goods = (props)=>{

	let item = props.location.item;


	function Tianjia(){
		props.shoucang.push(item)
		let idx = item.id
		console.log(idx)

		for(let i=0; i<props.shoucang.length-1; i++){
			if(props.shoucang[i].id === idx){
				console.log('重复收藏')
				alert('亲，重复收藏哦！')
				props.shoucang.splice(i,1)
			}
		}
	}
	

    return(
        <div className='goods'>
		  <div className='content'>
			<img src={item.img} />
			<p>{item.title}</p>
			<p>￥{item.price}</p>
			<NavLink to='/'>
			<div className='fanhui'>
				<i><i className='iconfont icon-return' style={{color:'white',position:'fixed',fontSize:'25px'}}></i></i>
			</div>
			</NavLink>
			<div className='gowuche'><i className='iconfont icon-gouwuche1' style={{color:'white',position:'fixed',fontSize:'28px'}}></i></div>
		  </div>
		  <div className='bar'>
			<div className='bar_bar'>
				<div className='bar_dianpu'>
					<i className='iconfont icon-dianpu'></i>
					<div>店铺</div>
				</div>
				<div className='bar_shoucang' onClick={()=>Tianjia()}>
					<i className='iconfont icon-shoucang'></i>
					<div>收藏</div>
				</div>
				<div className='bar_gouwuche' >
					<i className='iconfont icon-gouwuche1'></i>
					<div>购物车</div>
				</div>
			</div>
			<div className='bar_buy'>加入购物车</div>
			<div className='bar_join'>立即购买</div>
		  </div>
		</div>
	)
}

export default connect(store=>{
	return store;
})(Goods)