
let storetree={
    datalist:[],
    shoucang:[],
    useinfo:''
}

const reducer = (state=storetree,action)=>{
    switch(action.type){
        case 'LOGIN':
            return Object.assign({},state,{useinfo:action.username})
        case 'GOODS':
            return Object.assign({},state,{datalist:action.datalist})       
        case 'SHOUCANG':
            return Object.assign({},state,{shoucang:[...state.shoucang,action.index]})
        case 'DEL':
            for(let i=0; i<state.shoucang.length; i++){
                if(action.item === state.shoucang[i]){
                    state.shoucang.splice(i,1)
                }
            }   
            return Object.assign({},state)
        default:
            return state;
    }
}

export default reducer